.app {
  background: white;
  color: #1c94e0;
}

.preview {
  height: calc(220px - 100px);
  width: calc(100% - 100px);
  overflow: scroll;
  padding: 50px;
  background: #f5f8fa;
}

.preview-img {
  height: auto;
  width: 100%;
}

.preview-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.profile-image {
  height: 26px;
  width: 26px;
  border-radius: 20px;
  margin-left: 4px;
  margin-right: 2px;
  cursor: pointer;
}

.app-footer {
  height: 26px;
  padding: 8px;
  font-size: 14px;
  display: flex;

  border-top: 1px solid #e6ecf0;
}

.flex-pad {
  flex-grow: 1;
}

.btn {
  margin-left: 4px;
  margin-right: 4px;
  color: white;
  background: #1c94e0;
  border: 1px solid #1c94e0;
  border-radius: 50px;
  box-shadow: none;
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;
  line-height: 20px;
  padding: 2px 16px;
  position: relative;
  text-align: center;
  white-space: nowrap;
}

.btn-disabled {
  cursor: not-allowed;
  background: #999;
  border: 1px solid #999;
}

.btn-wait {
  cursor: wait;
  background: #1c94e0;
  border: 1px solid #1c94e0;
}

.btn-outline {
  color: #1c94e0;
  border: 1px solid #1c94e0;
  background: transparent;
}
